@mixin w {
  @media only screen and (max-width: "1599px") {
    @content; } }
@mixin x {
  @media only screen and (max-width: "1365px") {
    @content; } }
@mixin d {
  @media only screen and (max-width: "1199px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin a {
  @media only screen and (max-width: "639px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@900&family=Public+Sans:wght@400;500;600;700&display=swap');
@mixin f {
  font-family: 'Public Sans', sans-serif; }
@mixin fm {
  font-family: 'Merriweather', serif; }

$wh: #fff;
$purple: #7737FF;
$orange: #FF9900;
$pink: #FB5392;
$blue: #20DAF1;
//$green: #69C85A
$green: #52cb95;
$green-dark: #3F9E2E;
$red: #FF570F;
$bg: #FAFBFF;
$bg-dark: #F3F6FF;
$text: #4C5D77;

// dark theme
$bl: #14141B;
$bl-light: #1E1E27;
$dark: #00006D;
$gray: #9BAABF;
$wh-dark: #F7FAFC;

html {
  // height: 100%
  &.no-scroll {
    overflow: hidden; } }

body {
  min-width: 375px;
  background: $bg;
  @include f;
  font-size: 16px;
  letter-spacing: -.015em;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $dark;
  &.dark {
    background: $bl;
    color: $wh-dark; }
  &.no-scroll {
    overflow: hidden; } }
video {
  width: 100%;
  max-height: 700px; }
button,
input,
textarea,
select {
  @include f; }

.page {
  overflow: hidden; }

.center {
  max-width: 1332px;
  margin: 0 auto;
  padding: 0 42px;
  @include t {
    padding: 0 40px; }
  @include m {
    padding: 0 32px; } }

a.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center; }

.btn {
  min-width: 154px;
  height: 64px;
  padding: 0 32px;
  border-radius: 32px;
  @include f;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: -.01em;
  transition: all .25s;
  @include m {
    height: 56px;
    border-radius: 28px;
    font-size: 16px; }
  .icon,
  &__text {
    display: inline-block;
    vertical-align: middle; }
  .icon-arrow-right {
    margin: 0 30px 0 6px;
    font-size: 21px;
    @include m {
      margin: 0 20px 0 0;
      font-size: 16px; } }
  &_purple {
    background: $purple;
    color: #F7FAFC;
    .icon {
      fill: $wh; }
    &:hover {
      background: #6E34EA; } }
  &_border {
    padding: 0 30px;
    border: 2px solid rgba($purple, .24);
    color: $purple;
    &:hover {
      border-color: $purple; } }
  &_red {
    background: $red;
    color: #F7FAFC;
    &:hover {
      background: #FF5207; } }
  &_green {
    background: $green;
    color: #F7FAFC;
    &:hover {
      background: #459836; } }
  &_sm {
    height: 48px;
    border-radius: 24px;
    font-size: 16px;
    @include m {
      height: 48px;
      border-radius: 24px; } } }

.header {
  position: relative;
  z-index: 10;
  margin-bottom: 24px;
  padding: 48px 0;
  letter-spacing: 0;
  @include t {
    margin-bottom: 40px;
    padding: 32px 0; }
  @include m {
    margin-bottom: 24px; }
  &__center {
    display: flex;
    align-items: center;
    @include d {
      padding: 0 48px; }
    @include t {
      padding: 0 40px; } }
  &__logo {
    font-size: 0;
    @include t {
      position: relative;
      z-index: 15;
      margin-right: auto; } }
  &__logo &__pic {
    width: 100px;
    &_white {
      display: none; } }
  &__wrap {
    display: flex;
    align-items: center;
    flex-grow: 1;
    padding-left: 96px;
    @include t {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 10;
      flex-direction: column;
      align-items: stretch;
      width: 100%;
      min-height: 100vh;
      padding: 152px 40px 72px;
      background: $bg;
      transform: translateX(100%);
      transition: transform .3s;
      &.visible {
        transform: translateX(0); } }
    @include m {
      padding: 110px 32px 56px; }
    @include s {
      padding-top: 136px; } }
  &__nav {
    display: flex;
    margin-right: auto;
    @include t {
      flex-direction: column;
      margin: 0 0 auto; }
    @include m {
      flex-direction: row; }
    @include s {
      flex-direction: column; } }
  &__link {
    position: relative;
    font-weight: 700;
    color: $text;
    transition: color .25s;
    @include t {
      padding-right: 40px;
      @include fm;
      font-size: 36px;
      line-height: (56/36);
      font-weight: 900;
      color: $dark;
      &:after {
        content: "";
        position: absolute;
        top: 16px;
        right: 6px;
        width: 12px;
        height: 21px;
        background: url("data:image/svg+xml,%3Csvg width='8' height='14' viewBox='0 0 8 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.292893 0.292893C-0.0976315 0.683417 -0.0976315 1.31658 0.292893 1.70711L5.58579 7L0.292893 12.2929C-0.0976315 12.6834 -0.0976315 13.3166 0.292893 13.7071C0.683417 14.0976 1.31658 14.0976 1.70711 13.7071L7.70711 7.70711C8.09763 7.31658 8.09763 6.68342 7.70711 6.29289L1.70711 0.292893C1.31658 -0.0976311 0.683417 -0.0976311 0.292893 0.292893Z' fill='%237737FF'/%3E%3C/svg%3E%0A") no-repeat 50% 50% / 100% auto; } }
    @include m {
      font-size: 24px;
      line-height: (40/24);
      &:after {
        top: 13px;
        width: 8px;
        height: 14px; } }
    &:before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 2px;
      width: 0;
      height: 1px;
      background: $green;
      transition: width .25s;
      @include t {
        display: none; } }
    &:hover {
      color: $green;
      &:before {
        width: 100%; } }
    @include nl {
      margin-right: 48px;
      @include t {
        margin: 0 0 48px; }
      @include m {
        margin: 0 32px 0 0; }
      @include s {
        margin: 0 0 48px; } } }
  &__control {
    display: flex;
    align-items: center;
    @include t {
      flex-direction: column-reverse;
      margin-top: 24px; } }
  &__login {
    display: inline-flex;
    align-items: center;
    margin-right: 36px;
    @include t {
      margin: 24px 0 0; }
    .icon {
      margin-left: 20px;
      font-size: 14px;
      fill: $green; } }
  &__login &__text {
    position: relative;
    font-weight: 700;
    color: $text;
    transition: color .25s;
    &:before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 2px;
      width: 0;
      height: 1px;
      background: $green;
      transition: width .25s; } }
  &__login:hover &__text {
    color: $green;
    &:before {
      width: 100%; } }
  &__btn {
    height: 56px;
    border-radius: 28px;
    font-size: 16px;
    @include t {
      min-width: 100%; } }
  &__burger {
    display: none;
    position: relative;
    z-index: 12;
    width: 48px;
    height: 48px;
    padding: 10px;
    background: $green;
    border-radius: 50%;
    font-size: 0;
    transition: opacity .25s;
    @include t {
      display: inline-block; }
    &:before,
    &:after {
      content: "";
      display: inline-block;
      width: 20px;
      height: 2px;
      background: $wh;
      border-radius: 2px;
      transition: transform .25s; }
    &:before {
      margin-bottom: 3px; }
    &:after {
      margin-top: 3px; }
    &.active {
      &:before {
        transform: translateY(4px) rotate(45deg); }
      &:after {
        transform: translateY(-4px) rotate(-45deg); } } } }

.section {
  @include nl {
    margin-bottom: 144px;
    @include x {
      margin-bottom: 120px; }
    @include m {
      margin-bottom: 104px; } } }

.title {
  margin-bottom: 32px;
  @include fm;
  font-size: 36px;
  line-height: (56/36);
  font-weight: 900;
  letter-spacing: -.01em;
  @include m {
    margin-bottom: 24px;
    font-size: 24px;
    line-height: (40/24); }
  &__link {
    border-bottom: 3px solid $green;
    color: $green;
    transition: border-color .25s;
    @include m {
      border-width: 2px; }
    &:hover {
      border-color: transparent; } }
  &_sm {
    font-size: 24px;
    line-height: (40/24); }
  &_big {
    font-size: 48px;
    line-height: (72/48);
    @include m {
      font-size: 36px;
      line-height: (56/36); } } }

.info {
  font-size: 24px;
  line-height: (40/24);
  color: $text;
  @include m {
    font-size: 20px;
    line-height: 2; } }

.main {
  position: relative;
  padding: 93px 0;
  @include x {
    padding: 80px 0; }
  @include d {
    margin-bottom: 120px;
    padding: 0;
    text-align: center; }
  @include m {
    margin-bottom: 104px; }
  &__wrap {
    max-width: 576px;
    @include d {
      max-width: 704px;
      margin: 0 auto 72px; }
    @include m {
      margin-bottom: 48px; } }
  &__info {
    margin-bottom: 64px;
    font-size: 24px;
    line-height: (40/24);
    @include m {
      margin-bottom: 48px; } }
  &__btns {
    display: flex;
    @include d {
      justify-content: center; }
    @include a {
      display: block; } }
  &__btn {
    @include a {
      min-width: 100%; }
    @include nl {
      margin-right: 32px;
      @include a {
        margin: 0 0 24px; } } }
  &__preview {
    position: absolute;
    top: 0;
    right: calc(50% - 900px);
    pointer-events: none;
    font-size: 0;
    opacity: 0;
    transform: translateX(100px);
    animation: image 1s forwards;
    @include x {
       right: calc(50% - 800px); }
    @include d {
      position: static; } }
  &__pic {
    @include x {
      width: 770px; }
    @include d {
      width: 848px; }
    @include t {
      width: 100%;
      max-width: 768px; } }
  &__video {
    @include x {
      max-height: 700px; }
    @include d {
      max-height: 700px; }
    @include t {
      max-height: 700px; } } }


@keyframes image {
  100% {
    transform: translateX(0);
    opacity: 1; } }

.quality {
