.details {
  &__center {
    max-width: 1236px; }
  &__item {
    display: flex;
    align-items: center;
    &:first-child {

      .details__pic {
        @include m {
          width: 500px; } } }
    &:nth-child(2) {

      .details__pic {
        @include m {
          width: 630px; } } }
    &:nth-child(3) {


      .details__pic {
        @include m {
          width: 630px; } } }
    &:nth-child(2n+1) {
