.switch {
  display: inline-block;
  position: relative;
  user-select: none;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  font-size: 0;
  transition: opacity .25s;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__in {
    display: inline-block; }
  &__box {
    position: relative;
    display: block;
    width: 56px;
    height: 32px;
    background: rgba($green, .08);
    border-radius: 16px;
    transition: all .25s;
    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 6px;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
      border-radius: 50%;
      border: 6px solid $green;
      transition: all .25s; } }
  &__preview {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 0; }
  &__pic_moon {
    display: none; }
  &:hover {
    opacity: .85; }
  &__flex {
    display: flex;
    justify-content: center;
    align-items: center; }
  &__category {
